r yang dapat kita gunakan untuk mengeksekusi program\n        javascript.\n    ")]),_vm._v(" "),_c('p',[_vm._v("Untuk pemanasan kita coba untuk mendefinisikan beberapa variabel di javascript dibawah.")]),_vm._v(" "),_c('p',[_c('vue-embed-gist',{attrs:{"gist-id":"28309e8c00d62ee28be0cb29c562a8be"}}),_vm._v(" "),_c('common-image',{attrs:{"src":"https://3.bp.blogspot.com/-D2hMt4pmY7s/Wc87OdafDoI/AAAAAAAABjE/Dz4X_s8S4oQfNT11beQ4M6QT8ZYJbyTlACLcBGAs/s640/fawf.PNG","caption":"Contoh pada browser mozilla, kamu dapat menyalin script yang ada diatas pada inputan di developer console browser"}})],1),_vm._v(" "),_c('p',[_vm._v("Berbeda dengan bahasa compiler, dalam variabel javascript kita bisa meredeklarasi variabel yang\n        semula bertipe integer (bilangan) menjadi bertipe string (kalimat/alfabet). Namun demi menjaga agar\n        programu lebih terjaga kualitasnya dan tidak pusing kedepanya, ada baiknya hal ini dihindari.\n        Dari gambar diatas kita juga dapat menampilkan nilai dari setiap variabel ke layar dengan\n        menggunakan perintah console.log();\n        Selain kita dapat mendeklarasikan variabel seperti string & integer. Kita juga bisa mendeklarasikan\n        array, object bahkan function sekaligus. Misal")]),_vm._v(" "),_c('p',[_c('vue-embed-gist',{attrs:{"gist-id":"9a908a634bfe982076c7f93e64fb0352"}})],1),_vm._v(" "),_c('p',[_c('strong',{staticClass:"nl-blocked"},[_vm._v("2. Mempersiapkan Text Editor")]),_vm._v("\n        Text editor merupakan alat untuk mempermudah kita untuk menuliskan program. Disini saya\n        merekomendasikan Sublime Text. Karena selain ringan applikasi ini dapat membuat kita menjadi sangat\n        produktif.\n        Setelah kamu menginstal sublime selanjutnya cobakah tekan ctrl+shift+p dan pilih install package\n        untuk menginstal package yang dapat mempermudah kerja kita.\n        beberapa plugin yang saya rekomendasikan antara lain. Emmet, HTML beautify & Javascript Beautify.\n        Untuk selanjutnya kamu bisa